.item {
    list-style: none;
    display: flex;
    flex-direction: column;
    font-size: 24px;
    background: linear-gradient(to right, rgba(240, 240, 192, 0.2), rgba(84, 84, 81, 0.2));
        border-radius: 4px;
        border: solid 2px #514646;
        box-shadow: 0px 5px 5px rgba(69, 71, 65, 0.6);
        padding: 15px;
       
      
}
.stat-list {
    display: flex;
    padding: 0px;
    text-align: center;
    gap: 15px;
    flex-wrap: wrap;
    justify-content: center;
}
.title {
    text-align: center;
}
.label {
    font-size: 24px;
        text-align: center;
        color: crimson;
}
.percentage {
    font-size: 24px;
        text-align: center;
        margin-top: 5px;
        color: #514646;
}